"list\">\n\n        <el-table-column label=\"福利\"\n                         align=\"center\"\n                         prop=\"user_type\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"text\"\n                       v-if='scope.row.user_type==1'\n                       @click=\"details(scope.row)\">保险公司</el-button>\n            <span v-else>\n              {{scope.row.user_type==1 ? '保险公司' : scope.row.user_type==2 ? '主机厂' : '其他类型'}}\n            </span>\n          </template>\n        </el-table-column>\n        <el-table-column prop=\"ucp_company\"\n                         label=\"名称\"\n                         align=\"center\"></el-table-column>\n        <el-table-column prop=\"company\"\n                         label=\"所属维修厂\"\n                         align=\"center\"></el-table-column>\n        <el-table-column prop=\"name\"\n                         label=\"车品牌\"\n                         align=\"center\"></el-table-column>\n        <el-table-column prop=\"type\"\n                         label=\"车类型\"\n                         align=\"center\"></el-table-column>\n        <el-table-column prop=\"series\"\n                         label=\"车排量\"\n                         align=\"center\"></el-table-column>\n        <el-table-column prop=\"plate\"\n                         label=\"车牌号\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"保单号\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n           \n            <span  v-if='scope.row.user_type==1'>\n              <el-popover placement=\"top-start\"\n                          title=\"保单号\"\n                          width=\"200\"\n                          trigger=\"hover\"\n                          :content=\"scope.row.policy_num\">\n                <el-button type=\"text\"\n                           slot=\"reference\"\n                           class=\"ellipsis\">{{scope.row.policy_num}}</el-button>\n              </el-popover>\n            </span>\n             <span v-else>无</span>\n          </template>\n        </el-table-column>\n          <el-table-column label=\"保单照片\"\n                         align=\"center\" >\n\n          <template slot-scope=\"scope\" >\n            \n            <el-button type=\"text\" v-if='scope.row.user_type==1' @click=\"details(scope.row)\">详情</el-button>\n            <span v-else>无 </span>\n            \n          </template>\n\n        </el-table-column>\n        <el-table-column align=\"center\"\n                         label=\"驳回理由\">\n          <template slot-scope=\"scope\">\n            <el-popover placement=\"top-start\"\n                        title=\"驳回理由\"\n                        width=\"200\"\n                        trigger=\"hover\"\n                        :content=\"scope.row.reason\">\n              <el-button type=\"text\"\n                         slot=\"reference\"\n                         class=\"ellipsis\">{{scope.row.reason}}</el-button>\n            </el-popover>\n          </template>\n        </el-table-column>\n        <el-table-column prop=\"sale_time\"\n                         label=\"售卡时间\"\n                         align=\"center\"></el-table-column>\n        <!-- <el-table-column label=\"操作\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"primary\"\n                       size=\"mini\"\n                       @click=\"through(scope.row)\">通过</el-button>\n            <el-button type=\"danger\"\n                       size=\"mini\"\n                       @click=\"rejected(scope.row)\">驳回</el-button>\n          </template>\n\n        </el-table-column> -->\n      </el-table>\n\n      <!-- 分页 -->\n      <div :class=\"list && list.length<=7?'page-nav':'page_center'\"\n           v-show=\"list && list.length>0\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n    <el-dialog title=\"详情\"\n               center\n               :visible.sync=\"detailsVisible\">\n      <el-form label-width=\"100px\"\n               v-if='detailsList'>\n        <el-form-item label=\"vin码 : \">\n          {{detailsList.vin}}\n        </el-form-item>\n        <el-form-item label=\"保单号 : \">\n          {{detailsList.policy_num}}\n        </el-form-item>\n        <el-form-item label=\"投保时间 : \">\n          {{detailsList.start_time}}\n        </el-form-item>\n        <el-form-item label=\"截止时间 : \">\n          {{detailsList.end_time}}\n        </el-form-item>\n        <el-form-item label=\"售卡时间 : \">\n          {{detailsList.sale_time}}\n        </el-form-item>\n\n      </el-form>\n      <div>\n        <span style=\"margin:0 20px 0 25px\">保单图片 : </span>\n\n        <img :src=\"item\"\n             v-for=\"(item,index) in detailsList.pc_img\"\n             :key=\"index\"\n             class=\"images\"\n             ref=\"images\">\n\n      </div>\n    </el-dialog>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport Viewer from '@/utils/Viewer'\nexport default {\n  data () {\n    return {\n      list: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      page: 0,\n      pageCount: 0,\n      thCurId: '',\n      authList: [],\n      threeAuthList: [],\n      throughLoading: false,\n      rejecteLoading: false,\n      detailsVisible: false,\n      multipleData: [],\n      detailsList: {}\n    }\n  },\n  created () {\n    this.init();\n  },\n\n  methods: {\n\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    init () {\n      this.$axios.post('admin/UserAudit/channelList', { token: this.token, page: this.page, status: 2, pay_status: 2 })\n        .then(res => {\n\n          this.list = res.data.data.list;\n\n          this.pageCount = res.data.data.rows;\n        }).catch(err => { })\n    },\n    details (row) {  //详情\n      this.detailsVisible = true\n      this.detailsList = row\n      this.$nextTick(() => {\n        Viewer(this.$refs.images)\n      })\n\n    },\n    handleSelectionChange (val) {  //全选/单选\n      let tmp = []\n      val.forEach(el => {\n        tmp.push({ ucp_id: el.ucp_id, uc_id: el.uc_id, user_type: el.user_type })\n      });\n\n      this.multipleData = tmp\n    },\n    //通过\n    through () {\n      if (this.multipleData && this.multipleData.length < 1) {\n        this.$message.error('请选择通过项')\n        return;\n      }\n      this.$confirm('此操作将通过审核, 是否继续?', '提示', {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        type: 'warning',\n        center: true\n      }).then(async () => {\n        try {\n\n          this.throughLoading = true\n          const res = await this.$axios.post('admin/UserAudit/channelAdopt', { token: this.token, data: this.multipleData })\n          this.throughLoading = false\n          if (res.data.code == 1) {\n            this.$message({ message: res.data.msg, type: \"success\" })\n            this.init()\n          } else {\n            this.$message.error(res.data.msg)\n          }\n        } catch (err) {\n          this.throughLoading = false\n          throw (err)\n        }\n      }).catch(() => { });\n    },\n    //驳回\n    rejecte () {\n      if (this.multipleData && this.multipleData.length < 1) {\n        this.$message.error('请选择驳回项')\n        return;\n      }\n      this.$prompt('请输入邮箱', '提示', {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        inputPattern: /\\S/,\n        inputErrorMessage: '请输入驳回理由'\n      }).then(async ({ value }) => {\n        try {\n\n          this.rejecteLoading = true\n          const res = await this.$axios.post('admin/UserAudit/channelReject', { token: this.token, data: this.multipleData, reason: value })\n          this.rejecteLoading = false\n          if (res.data.code == 1) {\n            this.$message({ message: res.data.msg, type: \"success\" })\n            this.init()\n          } else {\n            this.$message.error(res.data.msg)\n          }\n        } catch (err) {\n          this.rejecteLoading = false\n          throw (err)\n        }\n      }).catch(() => { });\n    }\n\n\n  },\n  mounted () {\n    var id = this.$route.query.id;\n    this.curId = id;\n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].son) {\n              if (arr[i].name == '保险客户') {\n                this.seCurId = arr[i].id;\n                this.threeAuthList = arr[i].son;\n              }\n              for (var j = 0; j < arr[i].son.length; j++) {\n                if (arr[i].action != arr[i].son[j].action) {\n                  arr[i].action = arr[i].son[0].action;\n                }\n                if (arr[i].son[j].name == '已驳回' && arr[i].name == '保险客户') {\n                  this.thCurId = arr[i].son[j].id;\n                }\n              }\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', { type: 'error' });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  },\n}\n</script>\n<style scoped>\n.ellipsis {\n  width: 70px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 2;\n  margin: 0 auto;\n}\n.img {\n  width: 70px;\n  height: 70px;\n}\n.images {\n  width: 100px;\n  height: 100px;\n  margin-right: 10px;\n  vertical-align: top;\n}\n.laybox2 ul {\n  margin: 33px auto;\n  border-bottom: 1px dashed #04bbfc;\n}\n\n.laybox2 {\n  padding: 60px;\n  font-size: 16px;\n}\n\n.laybox2 li {\n  width: 300px;\n  text-align: left;\n  margin: 33px auto;\n}\n\n.t-c:after {\n  content: \"\";\n  display: block;\n  height: 0;\n  clear: both;\n}\n\n.bot > div {\n  position: relative;\n}\n\ndiv.bot {\n  margin: 45px auto 35px auto;\n  height: 100px;\n}\n\ndiv.bot span {\n  display: inline-block;\n  width: 20%;\n}\n\ndiv.bot input {\n  display: inline-block;\n  width: 55%;\n  outline: none;\n  border: none;\n  border-bottom: 1px solid #aaa;\n}\n\ndiv.bot button {\n  width: 110px;\n  height: 36px;\n  margin-left: -55px;\n  position: absolute;\n  border-radius: 18px;\n  box-shadow: 4px 0px 6px rgba(177, 42, 19, 0.2);\n  border: none;\n  color: #fff;\n  left: 50%;\n  top: 60px;\n}\n\nbutton.reject {\n  background: rgba(237, 81, 55, 1);\n  margin-right: 30%;\n}\n\nbutton.pass {\n  background-color: #20a0ff;\n}\n\n.left {\n  float: left;\n  width: 50%;\n}\n\n.right {\n  float: right;\n  width: 50%;\n}\n.nowrap {\n  width: 120px !important;\n  text-align: center;\n  white-space: nowrap; /*规定文本不换行**/\n  text-overflow: ellipsis; /**显示省略符号来代表被修剪的文本。*/\n  overflow: hidden;\n  margin: 0 auto;\n}\n.regionDetail >>> .el-dialog__title {\n  font-size: 22px;\n}\n.regionDetail >>> .el-dialog__body {\n  padding: 0px 25px 30px;\n  border-top: solid 2px #eee;\n}\n</style>\n"],"sourceRoot":""}]);

